* {
  margin: 0;
}

.app__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.app_stats{
  display:flex;
  justify-content: space-between;
}

body{
  background-color: rgb(255, 179, 155);
}

.app{
  display: flex;
  justify-content: space-evenly;
  padding: 30px;
}

@media (max-width: 990px)
{
  .app
  {
    flex-direction: column
  }
}

.app_left{
  flex: 0.9;
}